import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TextToSpeech } from '@ionic-native/text-to-speech';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@IonicPage()
@Component({
    selector: 'page-result-of-a',
    templateUrl: 'result-of-a.html',
})
export class ResultOfAPage {

    a1: number;
    a2: number;
    res1Img: string;
    res2Img: string;
    result: string;
    stoped: boolean = false;
    data: string[][];
    preData = [];

    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, private tts: TextToSpeech) {
        this.preData = navParams.get('data');
        this.a1 = this.preData[0].num;
        this.a2 = this.preData[1].num;
        this.http.get("assets/data/a01result.json").map(res => res.json()).subscribe(result => {
            this.data = result;
            this.result = this.data[this.a1 - 1][this.a2 - 1];
        });
        this.res1Img = this.preData[0].img;
        this.res2Img = this.preData[1].img;
    }

    speak() {
        this.stoped = !this.stoped;
        if (this.stoped) {
            this.tts.speak({
                text: this.result,
                rate: 0.8
            })
                .then(() => console.log('Success'))
                .catch((reason: any) => console.log(reason));
        }
        else {
            this.stop();
        }

    }

    stop() {
        this.tts.speak("");
    }

    goHome() {
        this.navCtrl.popToRoot();
    }

    ngOnDestroy() {
        this.stop();
    }

}



