import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ResultOfAPage } from '../result-of-a/result-of-a';

@IonicPage()
@Component({
    selector: 'page-a02',
    templateUrl: 'a02.html',
})
export class A02Page {

    clicked: boolean = true;
    data: number;
    a2: number;
    imgSrc: any = [];
    public cssClass = new Array(12).fill("animated ");

    constructor(public navCtrl: NavController, public navParams: NavParams) {
        this.data = navParams.get('data');
        for (var i = 0; i < 12; i++) {
            this.imgSrc[i] = "assets/imgs/yellow.png";
        }
    }

    cantClick(i: number) {
        if (this.clicked) {
            this.a2 = Math.floor(Math.random() * 12 + 1);
            this.cssClass[i] += "flip";
            this.imgSrc[i] = "assets/imgs/yellow" + this.a2 + ".png";
            this.clicked = false;
            this.data[1].num = this.a2;
            this.data[1].img = this.imgSrc[i];
            //setTimeout(() => {
            //    this.imgSrc[i] = "assets/imgs/yellow" + this.a2 + ".png";
            //}, 570);

        }
    }

    goResult() {
        if (!this.clicked) {
            this.navCtrl.push(ResultOfAPage, { data: this.data });
        }
        else {
            for (var i = 0; i < 12; i++) {
                this.cssClass[i] = "animated " + "tada";
            }
        }
    }

}



