import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { A02Page } from '../a02/a02';

@IonicPage()
@Component({
    selector: 'page-a-1',
    templateUrl: 'a01.html',
})
export class A01Page {

    clicked: boolean = true;
    a1: number;
    imgSrc: any = [];
    public cssClass = new Array(12).fill("animated ");
    data = [
        {
            num: 0,
            img: ""
        },
        {
            num: 0,
            img: ""
        }
    ]


    constructor(public navCtrl: NavController, public navParams: NavParams) {
        for (var i = 0; i < 12; i++) {
            this.imgSrc[i] = "assets/imgs/red.png";
        }
    }

    cantClick(i: number) {
        if (this.clicked) {
            this.a1 = Math.floor(Math.random() * 12 + 1);
            this.cssClass[i] += "flip";
            this.clicked = false;
            this.imgSrc[i] = "assets/imgs/red" + this.a1 + ".png";
            this.data[0].num = this.a1;
            this.data[0].img = this.imgSrc[i];
            //setTimeout(() => {
            //    this.imgSrc[i] = "assets/imgs/red" + this.a1 + ".png";
            //}, 570);
            
        }
    }

    goA02() {
        if (!this.clicked) {
            this.navCtrl.push(A02Page, { data: this.data });
        }
        else {
            for (var i = 0; i < 12; i++) {
                this.cssClass[i] = "animated " + "tada";
            }
        }
    }

}



