import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { A01Page } from '../a01/a01';

@Component({
    selector: 'page-home',
    templateUrl: 'home.html'
})
export class HomePage {

    doorArray = ["夫妻宮", "子女宮", "父母宮", "兄弟宮", "奴僕宮", "田宅宮", "命宮", "官祿宮", "疾惡宮", "財帛宮", "福德宮", "遷移宮"];
    imgArray = new Array(12).fill("assets/imgs/blue");
    title: string = "易經天星占卜";

    constructor(public navCtrl: NavController) {
        for (let i = 0; i < 12; i++) {
            this.imgArray[i] = this.imgArray[i] + (i + 1) + ".png";
        }
    }

    goPage(i) {
        if (i == 0) {
            this.navCtrl.push(A01Page);
        }
        
    }
}



